










import {Component, Vue, Watch} from 'vue-property-decorator';
import store from "@/store";
@Component({

})
export default class TheNotification extends Vue {
  $store:any=store;
  messageText = '';
  boxClass:null | object = null;

  get notificationInfo() {
    return this.$store.state.mod_globalView.notification;
  }
  get classShowNotification() {
    return this.notificationInfo ? 'the-notification--show' : ''
  }

  @Watch('notificationInfo')
  changeInfo(newNote:object|null) {
    const info = this.notificationInfo;
    if(info) {
      this.messageText = info.message;
      this.boxClass = {
        // 'bg-error': info.type as notification_type === notification_type.ERROR,
        // 'bg-success': info.type as notification_type === notification_type.SUCCESS,
        // 'bg-warning': info.type as notification_type === notification_type.WARNING
        'bg-error': info.type === 'error',
        'bg-success': info.type === 'success',
        'bg-warning': info.type === 'warning'
      }
    }
    else {
      setTimeout(()=> {
        this.messageText = '';
        this.boxClass = null
      }, 1000)
    }
  }

}
