






















import {Component, Vue} from 'vue-property-decorator';
import store from "@/store";
@Component
export default class TheLanguage extends Vue {
  $store:any=store;
  isOpen = false;
  get currentLang() {
    return this.$store.state.mod_globalView.siteLanguage
  }
  get allCountries() {
    return this.$store.state.mod_globalView.countries
  }
  get languageList() {
    const translations = Object.keys(this.$i18n.messages);
    return this.allCountries.reduce((acc:any[], el:any)=> {
      if(translations.includes(el.langIso) || translations.includes(el.iso2)) {
        acc.push({
          name: el.langNative || el.nameNative || el.name,
          code: el.langIso || el.iso2,
          flagClass: el.iso2
        })
      }
      return acc
    }, [])
  }
  setLang(lang: string) {
    this.$store.dispatch('mod_globalView/SET_siteLanguage', {lang, to: this.$route});
    this.isOpen = false;
  }
}
