






























import {Component, Vue} from 'vue-property-decorator';

import TheLanguage    from '@/components/the-language.vue';
import TheHeaderMenu  from "@/components/the-header/the-header-menu.vue";
import TheSocialList  from "@/components/the-social-list.vue";
import TheHeaderCart  from "@/components/the-header/the-header-cart.vue";
//import UserAvatar     from "@/components/user/user-avatar.vue";
import TheHeaderProfile from "@/components/the-header/the-header-profile.vue";
import TheLogo        from "@/components/the-logo.vue";
import store from "@/store";
@Component({
  components: {TheLogo, TheHeaderProfile, TheSocialList, TheHeaderMenu, TheLanguage, TheHeaderCart}
})
export default class TheHeader extends Vue {
  $store:any=store;
  ready() {
    //window.addEventListener('resize', this.hideMenu);
  }
  beforeDestroy() {
    //window.removeEventListener('resize', this.hideMenu);
  }

  get userFullName() {
    return this.$store.getters['mod_user/fullName']
  }
  get companyInfo() {
    return this.$store.state.mod_company.companyInfo;
  }
  get phoneLink() {
    if(this.companyInfo) {
      let noSpaces = Vue.filter('noSpaces');
      return noSpaces(`tel:+${this.companyInfo.phonePrefix}${this.companyInfo.phone}`);
    }
    else return ''
  }
  mobMenu = false;

  hideMenu() { //close mobile menu when screen size changes
    this.mobMenu = false;
  }
}
