
























import {Component, Vue} from 'vue-property-decorator';
import TheSocialList from "@/components/the-social-list.vue";
import TheHeaderProfile from "@/components/the-header/the-header-profile.vue";
import store from "@/store";

@Component({
  components: {TheHeaderProfile, TheSocialList}
})
export default class TheHeaderMenu extends Vue {
  $store:any=store;
  menuList = [
    {title: 'Event & Meetings', url: '/'},
    {title: 'Gallery', url: '/'},
  ];
  get isOpenMenu() {
    return this.$store.state.mod_globalView.isDisableScroll
  };

  toggleMenu() {
    this.$store.dispatch('mod_globalView/SET_isDisableScroll', !this.isOpenMenu)
  }
  openLogin() {
    this.$store.dispatch('mod_globalView/OPEN_modal', 'login')
  }
}
