import {storeActions, i_search, i_filterSettings, i_product} from '@/store/store'
import {out_webProductsAll, in_webProductsList, RequestError, out_webProductsList} from 'visbook-api'
import {i_select, i_checkinPeriod}          from '@/core/declarations.ts';
import {requestVB, toRouterUrl} from "@/core/helpers";
import Vue from 'vue'

const namespaced = true;

const state:i_search = {
  allRooms: [],
  filterSettings: {
    checkIn: '',
    checkOut: '',
    checkInDate: null,
    checkOutDate: null,
    nights: 0,
    adults: '1',
    child: '0',
    allPersons: 1
  },
  filterSettingsDynamic: {
    roomType: [],
    sortBy: { label: 'Name A-Z', value: '1'}
  },
  roomTypeList: [],
  sortByList: [
    { label: 'Name A-Z', value: '1'},
    { label: 'Name Z-A', value: '2'},
    { label: 'Price up', value: '3'},
    { label: 'Price down', value: '4'}
  ]
};

const getters = {
  GET_filteredRooms(state:i_search) {
    let sortedList = [...state.allRooms];
    doSortList(sortedList);
    return filterType(sortedList);

    function filterType(list:i_product) {
      const selectedName = state.filterSettingsDynamic.roomType.map((i)=> i.label);
      return (selectedName.length)
        ? list.filter((room:i_product)=> selectedName.includes(room.name))
        : list;
    }
    function doSortList(list:i_product) {
      switch (state.filterSettingsDynamic.sortBy.value) {
        case '1':
          list.sort((a:i_product, b:i_product)=> a.name > b.name ? 1 : -1);
          break;
        case '2':
          list.sort((a:i_product, b:i_product)=> a.name > b.name ? -1 : 1);
          break;
        case '3':

          break;
        case '4':

          break;
      }
    }
  }
};

const mutations = {
  set_roomTypeList(state:i_search, list:i_select[]) {
    state.roomTypeList = list;
  },
  set_roomTypeSelected(state:i_search, list:i_select[]) {
    state.filterSettingsDynamic.roomType = list;
  },
  set_sortBySelected(state:i_search, sortBy:i_select) {
    state.filterSettingsDynamic.sortBy = sortBy;
  },

  set_filterSettings(state:i_search, nights:i_filterSettings) {
    state.filterSettings = nights;
  },
  set_allRooms(state:i_search, list:any[]) {
    state.allRooms = list;
  },
};

const actions = {
  GET_roomTypeList({commit}:storeActions) {
    return requestVB(Vue.prototype.$visbook.GET_webProductsAll)
      .then((response:out_webProductsAll[]) => {
        let webProductIdList:i_select[] = [];
        response.forEach((item:out_webProductsAll) => {
          webProductIdList.push({
            label: item.name,
            value: item.webProductId
          })
        });
        commit('set_roomTypeList', webProductIdList);
        return webProductIdList;
      })
      .catch((error:RequestError)=> {
        console.log(error);
        //this.inputEmail.error = error.message;
      })
  },
  GET_webProductsList({commit}:storeActions, inData:in_webProductsList) {
    requestVB(Vue.prototype.$visbook.GET_webProductsList, inData)
      .then((response:out_webProductsList) => {
        commit('set_allRooms', [...response.accommodations,...response.activities,...response.events])
      })
      .catch((error:RequestError)=> {
        console.log(error);
        //this.inputEmail.error = error.message;
      })
  },
  SET_filterSettings({commit}:storeActions, settings:any) {
    const date = Vue.filter('date');
    const filterSettings:i_filterSettings = {
      checkInDate: settings.checkinPeriod.checkIn,
      checkOutDate: settings.checkinPeriod.checkOut,
      checkIn: date(settings.checkinPeriod.checkIn),
      checkOut: date(settings.checkinPeriod.checkOut),
      nights: calcNights(settings.checkinPeriod.checkIn, settings.checkinPeriod.checkOut),
      adults: settings.adults,
      child: settings.child,
      allPersons: +settings.adults + +settings.child
    };
    commit('set_filterSettings', filterSettings);

    function calcNights(checkIn:Date, checkOut:Date):number {
      return (checkOut.getTime() - checkIn.getTime())/86400000 // days
    }
  },

  SET_roomTypeSelected({commit}:storeActions, list:i_select[]) {
    commit('set_roomTypeSelected', list)
  },
  SET_sortBySelected({commit}:storeActions, sort:i_select) {
    commit('set_sortBySelected', sort)
  },
};

export default {
  namespaced,
  state,
  getters,
  mutations,
  actions
}
