









import {Vue, Component, Prop, Watch} from 'vue-property-decorator';
@Component
export default class DetailsInfo extends Vue {
  @Prop({type: Boolean, default: false}) showInfo!:boolean;

  detailsHeight = '0px';

  @Watch('showInfo')
  calcElHeight() {
    let el = this.$refs.detailsEl as HTMLDivElement;
    this.detailsHeight = el.scrollHeight + 'px'
  }

}
