import Vue from 'vue'
import store from '@/store'
import router from '@/router'
import {AxiosResponse} from 'axios'
import {RequestError}         from 'visbook-api';

const throttleEv = function (func: ()=> void, ms?:number) {
  let isThrottled = false;
  let savedArgs:any;
  let savedThis:any;
  let delay = ms || 33; //30Hz

  function wrapper() {
    if (isThrottled) {
      savedArgs = arguments;
      savedThis = this;
      return;
    }

    func.apply(this, [...arguments] as []);
    isThrottled = true;
    setTimeout(function() {
      isThrottled = false;
      if (savedArgs) {
        wrapper.apply(savedThis, savedArgs);
        savedArgs = savedThis = null;
      }
    }, delay);
  }
  return wrapper;
};

const deepCopy = function (object:object) {
  return JSON.parse(JSON.stringify(object))
};

const requestVB = function (func:(...funcArg: any)=> Promise<AxiosResponse>, ...funcArg:any): Promise<any> {
  store.dispatch('mod_globalView/INC_requestLoader');
  let fb = func.bind(Vue.prototype.$visbook);
  return fb(...funcArg)
    .then((data:any)=> data)
    .catch((err:RequestError)=> {
      const r = router as any;
      //console.log('requestVB', func, err.message, err.response);
      const isUserLogin = store.getters['mod_user/isAuthorized'];
      const isUnauthorizedRes = err.status === 401;
      const isNotLoginPage = r.history.current.name !== 'login';

      if( isUserLogin && isUnauthorizedRes && isNotLoginPage) store.dispatch('mod_user/EVENT_logOut');
      throw err
    })
    .finally(()=> { store.dispatch('mod_globalView/DEC_requestLoader') })
};

const toRouterUrl = function (name:string) {
  const r = router as any;
  const path = r.history.current;
  router.push({
    name: name,
    params: path.params,
    query: path.query
  })
};

const convertToDateFormat = function(date:Date|null):string {
  if(!date) return '';
  const day = twoDigits(date.getDate());
  const month = twoDigits(date.getMonth()+1);

  return `${date.getFullYear()}-${month}-${day}`
};

const twoDigits = function(number:number):string {
  const numString = number.toString();
  return numString.length > 1
    ? numString
    : `0${numString}`
}

export {
  throttleEv,
  deepCopy,
  requestVB,
  toRouterUrl,
  convertToDateFormat,
  twoDigits
}
