

















import {Component, Vue} from 'vue-property-decorator';
import DetailsInfo   from "@/components/different/details-info.vue";
import {toRouterUrl}  from '@/core/helpers.ts';
import store from "@/store";
@Component({
  components: {DetailsInfo}
})
export default class TheHeaderProfile extends Vue {
  $store:any=store;
  showList = false;
  numOrders = 2;

  get userFullName() {
    return this.$store.getters['mod_user/fullName']
  }

  logout() {
    this.$store.dispatch('mod_user/EVENT_logOut')
      .then(()=> {  })
      .catch((err:any)=> {
        console.log("logout err", err);
      });
  }
}
