import Vue from "vue";

const ClickOutside = {  //custom directive that allows to assign global click handler which will only be executed outside of element
  bind: function(el: any, binding: any, vnode: any){
    el.clickOutsideEvent = function(event: Event){
      if(el == event.target) return;
      if(el.contains(event.target)) return;
      if(vnode.hasOwnProperty(binding.expression)) vnode.context[binding.expression](event, el);  //event handler is method of the component
      else if(typeof binding.value === 'function') binding.value.bind(vnode.context)(event, el);  //event handler is provided as anonymous function
      else (function(){ eval(binding.expression); }).call(vnode.context);  //execute expression in component context
    };
    document.body.addEventListener('click', el.clickOutsideEvent)
  },
  unbind: function(el: any){
    document.body.removeEventListener('click', el.clickOutsideEvent)
  },
};

Vue.directive('click-outside', ClickOutside);
