    showError = false;

    get showRequestLoader() {
      return this.$store.state.mod_globalView.requestLoader;
    }
    get isDisableScroll() {
      return this.$store.state.mod_globalView.isDisableScroll;
    }
    get showModal() {
      return this.$store.state.mod_globalView.modalComponent;
    }
    getCompany() {
      this.$store.dispatch('mod_company/GET_companyInfo')
        .then(()=>    { this.showError = false })
        .catch(()=>   { this.showError = true })
        .finally(()=> { this.hideSplashscreen() })
    }
    getSetupInfo() {
      this.$store.dispatch('mod_company/GET_setupInfo')
    }
    hideSplashscreen() {
      let loader = document.getElementById('page-loader');
      setTimeout(()=> {
        if(loader) loader.remove();
      }, 300)
    }
    getUserProfile() {
      this.$store.dispatch('mod_user/GET_userProfile')
    }
    setCountryList() {
      this.$store.dispatch('mod_globalView/SET_countries', Countries)
    }
