










































import {Component, Vue} from 'vue-property-decorator';
import {RequestError, out_webProductsAll}   from 'visbook-api';
import {convertToDateFormat}   from '@/core/helpers.ts';
import {i_select, i_checkinPeriod}          from '@/core/declarations.ts';
import HotelDatePicker  from '@/components/forms/hotel-date-picker.vue';
import store from "@/store";
interface i_searchParams {
  checkinPeriod: i_checkinPeriod
  discount: string
}

@Component({
  components: {HotelDatePicker}
})
export default class QuickBooking extends Vue {
  $store:any=store;
  created() {
    this.getRoomTypeList();
  }

  searchParams:i_searchParams = {
    checkinPeriod: {
      checkIn: null,
      checkOut: null
    },
    discount: ''
  };

  errors: {[key: string]: string} = {
    dates: '',
    accommodations: '',
    discount: ''
  };

  get webProductIdList():i_select[] {
    return this.$store.state.mod_search.roomTypeList;
  }
  get selectedRoomType():i_select[] {
    return this.$store.state.mod_search.filterSettingsDynamic.roomType;
  }
  updateRoomType(type:i_select[]) {
    this.$store.dispatch('mod_search/SET_roomTypeSelected', type)
  }
  getRoomTypeList() {
    this.$store.dispatch('mod_search/GET_roomTypeList')
  }
  search() {
    let query:{[key: string]: string} = {};
    const checkinPeriod = this.searchParams.checkinPeriod;
    const route = this.$route;
    /*create query params*/
    if(checkinPeriod.checkIn) query.checkIn = convertToDateFormat(checkinPeriod.checkIn);
    if(checkinPeriod.checkOut) query.checkOut = convertToDateFormat(checkinPeriod.checkOut);
    if(this.selectedRoomType.length) {
      const webProductList = this.selectedRoomType.map((el)=> el.value);
      query.webProductId = JSON.stringify(webProductList);
    }
    if(this.searchParams.discount) query.discount = this.searchParams.discount;
    /*create route*/
    this.$router.push({
      name: 'search',
      params: route.params,
      query: {
        ...route.query,
        ...query
      }
    });
  }
}
