import Vue from 'vue'
import Router, {Route} from 'vue-router'
import store  from './store'
import i18n   from './i18n'

import PageHome    from '@/pages/page-home.vue';
Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    //meta: title is used for page titles, parent is a "parent" page name used in breadcrumbs
    {
      path: '/:entity(\\d+)/',            name: 'home',     meta: {titleLang: 'pg-title_default'},
      component: PageHome
    },{
      path: '/:entity(\\d+)/search',      name: 'search',   meta: {titleLang: 'pg-title_search',    parent: 'home'},
      component: () => import('@/pages/page-search.vue')
    },
    //{
    //   path: '/:entity(\\d+)/checkout',    name: 'checkout', meta: {titleLang: i18n.t('pg-title_checkout'),  parent: 'home'},
    //   component: () => import('./views/Checkout.vue')
    // },{
    //   path: '/:entity(\\d+)/order',       name: 'order',    meta: {titleLang: i18n.t('pg-title_order'),     parent: 'home'},
    //   component: () => import('./views/Order.vue')
    // },
    /*errors*/
    {
      path: '/:entity(\\d+)/*',
      redirect: {name: 'NotFound'}
    },{
      path: '/no-entity',                 name: 'NoEntity', meta: {titleLang: 'pg-title_error'},
      component: () => import('@/pages/page-no-entity.vue')
    },{
      path: '/:entity(\\d+)/not-found',   name: 'NotFound', meta: {titleLang: 'pg-title_error'},
      component: () => import('@/pages/page-no-found.vue')
    },{
      path: '*',
      redirect: {name: 'NoEntity'}
    }
  ]
});

router.beforeEach((to:Route, from:Route, next) => {

  checkedTitle(to);
  console.log('store', store);
  const state = store.state as any;

  const lang = state.mod_globalView.siteLanguage;

  if(!lang.length) {
    //console.log('setDefaultLang');
    setDefaultLang(to);
    return
  }
  //console.log('to.query.lang', to.query.lang);
  to.query.lang
    ? next()
    : next({path: to.path, query: {...to.query, lang}});

  function checkedTitle(to:Route) {
    const title = i18n.t(to.meta.titleLang) as string;
    to.meta.title = title;
    document.title = title;
  }
});


function setDefaultLang(currentUrl:Route) {
  //this.$route is not initilised at this point, so have to get data myself
  let lang, match = RegExp('[?&]lang=([^&]*)').exec(window.location.search);
  if(match) lang = decodeURIComponent(match[1].replace(/\+/g, ' '));
  //check cookies for saved language
  if(!lang || Object.keys(i18n.messages).indexOf(lang) === -1) {
    lang = Vue.prototype.$cookies.get('lang');
  }
  store.dispatch('mod_globalView/SET_siteLanguage', {'lang': lang || i18n.locale, to: currentUrl});
}

export default router;
