import {storeActions, i_globalView, i_siteLanguage, i_notification} from '@/store/store'
import Vue from 'vue'
import {out_company, i_country, out_setup, out_order, out_orderGroup, RequestError} from 'visbook-api'
import router from '@/router'
import i18n   from '@/i18n'

const namespaced = true;

const state:i_globalView = {
  requestLoader: 0,
  isDisableScroll: false,
  siteLanguage: '',
  countries: [],
  notification: null,
  modalComponent: ''
};

const getters = {
  GET_requestLoader(store:i_globalView) {
    return !!store.requestLoader
  }
};

const mutations = {
  dec_requestLoader (state:i_globalView) {
    state.requestLoader = --state.requestLoader!;
  },
  inc_requestLoader (state:i_globalView) {
    state.requestLoader = ++state!.requestLoader!;
  },
  set_isDisableScroll (state:i_globalView, value:boolean) {
    state.isDisableScroll = value;
  },
  set_siteLanguage (state:i_globalView, value:string) {
    state.siteLanguage = value;
  },
  set_countries (state:i_globalView, value:i_country[]) {
    state.countries = value;
  },
  set_notification (state:i_globalView, value:i_notification) {
    state.notification = value;
  },
  set_modal (state:i_globalView, value:string) {
    state.modalComponent = value;
  },
};

const actions = {
  DEC_requestLoader({commit}:storeActions) {
    commit('dec_requestLoader');
  },
  INC_requestLoader({commit}:storeActions) {
    commit('inc_requestLoader');
  },
  SET_isDisableScroll({commit}:storeActions, val:boolean) {
    commit('set_isDisableScroll', val);
  },
  SHOW_notification({commit}:storeActions, val:i_notification) {
    commit('set_notification', val);
    setTimeout(()=> {
      commit('set_notification', null);
    }, 5000)
  },
  SET_siteLanguage({commit}:storeActions, {lang, to}:i_siteLanguage) {
    commit('set_siteLanguage', lang);
    i18n.locale = lang;
    router.replace({path: to.path, query: {...to.query, lang}});
    Vue.prototype.$cookies.set('lang', lang);
    if(Vue.prototype.$visbook) Vue.prototype.$visbook.SET_language(lang);
  },
  SET_countries({commit}:storeActions, val:object[]) {
    commit('set_countries', val);
  },
  OPEN_modal({commit}:storeActions, componentName:string) {
    commit('set_isDisableScroll', true);
    commit('set_modal', componentName);
  },
  CLOSE_modal({commit}:storeActions) {
    commit('set_isDisableScroll', false);
    commit('set_modal', '');
  }
};

export default {
  namespaced,
  state,
  getters,
  mutations,
  actions
}
