import {storeActions, i_company, i_globalView} from '@/store/store';
import {vue} from "@/main";
import Vue from 'vue'
import {out_company, out_setup, RequestError} from 'visbook-api'
import {requestVB}       from '@/core/helpers.ts';

const namespaced = true;

const state:i_company = {
  companyInfo: null,
  companySettings: null
};

const getters = {

};

const mutations = {
  set_companyInfo(state:i_company, info:out_company) {
    state.companyInfo = info;
  },
  set_setupInfo (state:i_company, value:out_setup) {
    state.companySettings = value;
  },
  set_primaryColor (state:i_company, color:string) {
    const cssRoot:any = document.querySelector(':root');
    const rootStyles = getComputedStyle(cssRoot);
    const textDark = rootStyles.getPropertyValue('--text-dark');
    const textLight = rootStyles.getPropertyValue('--text-light');

    cssRoot.style.setProperty('--color-primary', color);
    cssRoot.style.setProperty('--color-primary-txt', isLightColor(color) ? textDark : textLight);

    function isLightColor(color:string):boolean {
      const colNum = (color[0] === '#')
        ? color.substr(1, (color.length - 1))
        : color;
      const r = parseInt(colNum.substr(0, 2), 16);
      const g = parseInt(colNum.substr(2, 2), 16);
      const b = parseInt(colNum.substr(4, 2), 16);
      const yiq = ((r * 299) + (g * 587) + (b * 114)) / 1000;
      return yiq >= 128
    }
  }
};

const actions = {
  GET_companyInfo({commit}:storeActions) {
    return Vue.prototype.$visbook.GET_company()
      .then((info:out_company)=> {
        commit('set_companyInfo', info);
        return info;
      })
      .catch((err:RequestError)=> {
        throw err;
      })
  },
  GET_setupInfo({commit}:storeActions) {
    Vue.prototype.$visbook.GET_setup()
      .then((info:any) => {
        commit('set_setupInfo', info);
        if(info.colors.mainColor) {
          commit('set_primaryColor', info.colors.mainColor);
        }
      })
      .catch((err:RequestError) => { throw err; });
  }
};

export default {
  namespaced,
  state,
  getters,
  mutations,
  actions
}
