import {vue} from "@/main";
import {storeActions, i_orders} from '@/store/store'
import {out_company, i_country, out_setup, out_order, out_orderGroup} from 'visbook-api'
import {requestVB}    from '@/core/helpers.ts';

const namespaced = true;

const state = {
  selectedOrder: {
    id: null,
    currency: ''
  },
  orders: [],
  orderGroups: []
};

const getters = {

};

const mutations = {
  set_selectedOrderCurrency(state:i_orders, currency:string) {
    state.selectedOrder.currency = currency;
  },
  set_orders(state:i_orders, ordersList:out_order[]) {
    state.orders = ordersList;
  },
  set_orderGroups(state:i_orders, ordersList:out_orderGroup[]) {
    state.orderGroups = ordersList;
  },
};

const actions = {
  CLEAR_selectedOrderCurrency({commit}:storeActions) {
    commit('set_selectedOrderCurrency', '');
  },
  SET_selectedOrderCurrency({commit}:storeActions, currency:string) {
    commit('set_selectedOrderCurrency', currency);
  },
  GET_orders({commit}:storeActions) {
    return Promise.all([
      requestVB(vue.$visbook.GET_orders),
      requestVB(vue.$visbook.GET_orderGroups)
    ])
      .then((order:any[])=> {
        commit('set_orders', order[0]);
        commit('set_orderGroups', order[1]);
        return true;
      })
      .catch((err:Error)=> {
        throw err;
      });
  }
};

export default {
  namespaced,
  state,
  getters,
  mutations,
  actions
}
