import {storeActions, i_cart, i_cartItem, i_product} from '@/store/store'


const namespaced = true;

const state = {
  selectedProducts: []
};

const getters = {
  // fullName(state:i_user) {
  //   if(state.userProfile) {
  //
  //   }
  //   else return '';
  // }
};

const mutations = {
  add_product(state:i_cart, product:i_cartItem) {
    state.selectedProducts.push(product);
  },
  rem_product(state:i_cart, id:number) {
    state.selectedProducts = [...state.selectedProducts.filter((el:i_cartItem)=> el.product.id !== id)];
  },
};

const actions = {
  ADD_product({commit}:storeActions, cartItem:i_cartItem) {
    commit('add_product', cartItem);
  },
  REM_product({commit}:storeActions, id:number) {
    commit('rem_product', id);
  },
};

export default {
  namespaced,
  state,
  getters,
  mutations,
  actions
}
