<template lang="pug">
  div(:class="['vue-tel-input', wrapperClasses, { disabled: disabled }]")
    button.btn.btn--icon(type="button"
      v-click-outside="clickedOutside"
      :title="activeCountry.name"
      :tabindex="dropdownOptions && dropdownOptions.tabindex ? dropdownOptions.tabindex : 0"
      :class="['vti__dropdown', { open: open }]"
      @keydown="keyboardNav"
      @click="toggleDropdown"
      @keydown.esc="reset"
    )
      span.vti__selection
        .c-flag(
          v-if="enabledFlags"
          :class="['vti__flag', activeCountry.iso2.toLowerCase()]"
        )
        span.vti__country-code(v-if="enabledCountryCode") +{{ activeCountry.dialCode }}
        slot(name="arrow-icon" :open="open")
          span.vti__dropdown-arrow.icon(:class="open ? 'icon-chevron-top' : 'icon-chevron-down'")

      ul.vti__dropdown-list(
        ref="list"
        v-show="open"
      )
        li(
          v-for="(pb, index) in sortedCountries"
          :key="pb.iso2 + (pb.preferred ? '-preferred' : '')"
          :class="['vti__dropdown-item', getItemClass(index, pb.iso2)]"
          @click="choose(pb, true)"
          @mousemove="selectedIndex = index"
        )
          .c-flag(
            v-if="enabledFlags"
            :class="['vti__flag', pb.iso2.toLowerCase()]"
          )
          strong(v-if="dropdownOptions && !dropdownOptions.disabledDialCode") +{{ pb.dialCode }}
          span.text-notate  {{ pb.name }}
          span(v-if="pb.nameNative").text-default.country-name_native
            | (
            span {{ pb.nameNative }}
            | )
    //-span.country-code +{{activeCountry.dialCode}}
    base-input( type="tel"
      ref="input"
      v-model="phone"
      :input-placeholder="inputPlaceholder"
      :show-actions="showActions"
      :required="required"
      :text-error="textError"
      @input="onInput"
      @send-form="$emit('send-form')"
      @input-validity="$emit('input-validity', $event)"
      :class="['vti__input', inputClasses]"
    )
    //-input
            ref="input"
            type="tel"
            v-model="phone"
            /:placeholder="parsedPlaceholder"
            /:disabled="disabled"
            /:required="required"
            /:autocomplete="autocomplete"
            /:autofocus="autofocus"
            /:name="name"
            /:class="['vti__input', inputClasses]"
            /:id="inputId"
            /:maxlength="maxLen"
            /:tabindex="inputOptions && inputOptions.tabindex ? inputOptions.tabindex : 0"
            @blur="onBlur"
            @input="onInput"
            @keyup.enter="onEnter"
            @keyup.space="onSpace"

</template>

<script>
  import PhoneNumber from 'awesome-phonenumber';
  import utils, { getCountry, setCaretPosition } from './utils';
  import BaseInput   from '@/components/forms/base-input.vue';
  import store from "@/store";

  function getDefault(key) {
    const value = utils.options[key];
    if (typeof value === 'undefined') {
      return utils.options[key];
    }
    return value;
  }
  // Polyfill for Event.path in IE 11: https://stackoverflow.com/a/46093727
  function getParents(node, memo) {
    const parsedMemo = memo || [];
    const { parentNode } = node;
    if (!parentNode) {
      return parsedMemo;
    }
    return getParents(parentNode, parsedMemo.concat(parentNode));
  }

  export default {
    name: 'BaseInputPhone',
    components: {BaseInput},
    store,
    directives: {
      // Click-outside by BosNaufal: https://github.com/BosNaufal/vue-click-outside
      'click-outside': {
        bind(el, binding, vNode) {
          // Provided expression must evaluate to a function.
          if (typeof binding.value !== 'function') {
            const compName = vNode.context.name;
            let warn = `[Vue-click-outside:] provided expression ${binding.expression} is not a function, but has to be`;
            if (compName) {
              warn += `Found in component ${compName}`;
            }
            console.warn(warn);
          }
          // Define Handler and cache it on the element
          const { bubble } = binding.modifiers;
          const handler = (e) => {
            // Fall back to composedPath if e.path is undefined
            const path = e.path
              || (e.composedPath ? e.composedPath() : false)
              || getParents(e.target);
            if (bubble || (path.length && !el.contains(path[0]) && el !== path[0])) {
              binding.value(e);
            }
          };
          el.__vueClickOutside__ = handler;
          // add Event Listeners
          document.addEventListener('click', handler);
        },
        unbind(el) {
          // Remove Event Listeners
          document.removeEventListener('click', el.__vueClickOutside__);
          el.__vueClickOutside__ = null;
        },
      },
    },
    props: {
      value: {
        type: String,
        default: '',
      },
      inputPlaceholder: {
        type: String,
        default: ''
      },
      showActions: {
        type: Boolean,
        default: false
      },
      textError: {
        type: String,
      },
      placeholder: {
        type: String,
        default: () => getDefault('placeholder'),
      },
      disabledFetchingCountry: {
        type: Boolean,
        default: () => getDefault('disabledFetchingCountry'),
      },
      disabled: {
        type: Boolean,
        default: () => getDefault('disabled'),
      },
      mode: {
        type: String,
        default: () => getDefault('mode'),
      },
      invalidMsg: {
        type: String,
        default: () => getDefault('invalidMsg'),
      },
      required: {
        type: Boolean,
        default: () => getDefault('required'),
      },
      allCountries: {
        type: Array,
        default: () => getDefault('allCountries'),
      },
      defaultCountry: {
        // Default country code, ie: 'AU'
        // Will override the current country of user
        type: String,
        default: () => getDefault('defaultCountry'),
      },
      enabledCountryCode: {
        type: Boolean,
        default: () => getDefault('enabledCountryCode'),
      },
      enabledFlags: {
        type: Boolean,
        default: () => getDefault('enabledFlags'),
      },
      preferredCountries: {
        type: Array,
        default: () => getDefault('preferredCountries'),
      },
      onlyCountries: {
        type: Array,
        default: () => getDefault('onlyCountries'),
      },
      ignoredCountries: {
        type: Array,
        default: () => getDefault('ignoredCountries'),
      },
      autocomplete: {
        type: String,
        default: () => getDefault('autocomplete'),
      },
      autofocus: {
        type: Boolean,
        default: () => getDefault('autofocus'),
      },
      name: {
        type: String,
        default: () => getDefault('name'),
      },
      wrapperClasses: {
        type: [String, Array, Object],
        default: () => getDefault('wrapperClasses'),
      },
      inputClasses: {
        type: [String, Array, Object],
        default: () => getDefault('inputClasses'),
      },
      inputId: {
        type: String,
        default: () => getDefault('inputId'),
      },
      dropdownOptions: {
        type: Object,
        default: () => getDefault('dropdownOptions'),
      },
      inputOptions: {
        type: Object,
        default: () => getDefault('inputOptions'),
      },
      maxLen: {
        type: Number,
        default: () => getDefault('maxLen'),
      },
      validCharactersOnly: {
        type: Boolean,
        default: () => getDefault('validCharactersOnly'),
      },
      customValidate: {
        type: [Boolean, RegExp],
        default: () => getDefault('customValidate'),
      },
      dynamicPlaceholder: {
        type: Boolean,
        default: () => getDefault('dynamicPlaceholder'),
      },
    },
    beforeCreate() {
      utils.options.allCountries = this.$store.state.mod_globalView.countries;
    },
    created() {
      if (this.value) {
        this.phone = this.value.trim();
      }
    },
    mounted() {
      this.initializeCountry()
        .then(() => {
          if (!this.phone
            && this.inputOptions
            && this.inputOptions.showDialCode
            && this.activeCountry.dialCode) {
            this.phone = `+${this.activeCountry.dialCode}`;
          }
          this.$emit('validate', this.phoneObject);
          this.$emit('onValidate', this.phoneObject); // Deprecated
        })
        .catch(console.error)
        .finally(() => {
          this.finishMounted = true;
        });
    },

    data() {
      return {
        phone: '',
        activeCountry: { iso2: '' },
        open: false,
        finishMounted: false,
        selectedIndex: null,
        typeToFindInput: '',
        typeToFindTimer: null,
        cursorPosition: 0,
      };
    },
    computed: {
      parsedPlaceholder() {
        if (!this.finishMounted) {
          return '';
        }
        if (this.dynamicPlaceholder) {
          const mode = this.mode || 'international';
          return PhoneNumber.getExample(this.activeCountry.iso2, 'mobile').getNumber(mode);
        }
        return this.placeholder;
      },
      parsedMode() {
        if (this.customValidate) {
          return 'input';
        }
        if (this.mode) {
          if (!['international', 'national'].includes(this.mode)) {
            console.error('Invalid value of prop "mode"');
          } else {
            return this.mode;
          }
        }
        if (!this.phone || this.phone[0] !== '+') {
          return 'national';
        }
        return 'international';
      },
      filteredCountries() {
        // List countries after filtered
        if (this.onlyCountries.length) {
          return this.getCountries(this.onlyCountries);
        }
        if (this.ignoredCountries.length) {
          return this.allCountries.filter(
            ({ iso2 }) => !this.ignoredCountries.includes(iso2.toUpperCase())
              && !this.ignoredCountries.includes(iso2.toLowerCase()),
          );
        }
        return this.allCountries;
      },
      sortedCountries() {
        // Sort the list countries: from preferred countries to all countries
        const preferredCountries = this.getCountries(this.preferredCountries)
          .map(country => ({ ...country, preferred: true }));
        return [...preferredCountries, ...this.filteredCountries];
      },
      phoneObject() {
        const result = PhoneNumber(this.phone, this.activeCountry.iso2).toJSON();
        Object.assign(result, {
          isValid: result.valid,
          country: this.activeCountry,
        });
        return result;
      },
      phoneText() {
        let key = 'input';
        if (this.phoneObject.valid) {
          key = this.parsedMode;
        }
        return this.phoneObject.number[key] || '';
      },
    },
    watch: {
      // eslint-disable-next-line func-names
      'phoneObject.valid': function (value) {
        if (value) {
          this.phone = this.phoneText;
        }
        this.$emit('validate', this.phoneObject);
        this.$emit('onValidate', this.phoneObject); // Deprecated
      },
      value() {
        this.phone = this.value;
      },
      open(isDropdownOpened) {
        // Emit open and close events
        if (isDropdownOpened) {
          this.$emit('open');
        } else {
          this.$emit('close');
        }
      },
      phone(newValue, oldValue) {
        const isValidCharactersOnly = this.validCharactersOnly && !this.testCharacters();
        const isCustomValidate = this.customValidate && !this.testCustomValidate();
        if (isValidCharactersOnly || isCustomValidate) {
          this.$nextTick(() => { this.phone = oldValue; });
        }
        else if (newValue) {
          if (newValue[0] === '+') {
            const code = PhoneNumber(newValue).getRegionCode();
            if (code) {
              this.activeCountry = this.findCountry(code) || this.activeCountry;
            }
          }
        }
        // Reset the cursor to current position if it's not the last character.
        if (this.cursorPosition < oldValue.length) {
          this.$nextTick(() => { setCaretPosition(this.$refs.input, this.cursorPosition); });
        }

      },
      activeCountry(value) {
        if (value && value.iso2) {
          this.$emit('country-changed', value);
        }
      },
    },

    methods: {
      initializeCountry() {
        return new Promise((resolve) => {
          /**
           * 1. If the phone included prefix (+12), try to get the country and set it
           */
          if (this.phone && this.phone[0] === '+') {
            const activeCountry = PhoneNumber(this.phone).getRegionCode();
            if (activeCountry) {
              this.choose(activeCountry);
              resolve();
              return;
            }
          }
          /**
           * 2. Use default country if passed from parent
           */
          if (this.defaultCountry) {
            const defaultCountry = this.findCountry(this.defaultCountry);
            if (defaultCountry) {
              this.choose(defaultCountry);
              resolve();
              return;
            }
          }
          const fallbackCountry = this.findCountry(this.preferredCountries[0])
            || this.filteredCountries[0];
          /**
           * 3. Check if fetching country based on user's IP is allowed, set it as the default country
           */
          if (!this.disabledFetchingCountry) {
            getCountry()
              .then((res) => {
                this.activeCountry = this.findCountry(res) || this.activeCountry;
              })
              .catch((error) => {
                console.warn(error);
                /**
                 * 4. Use the first country from preferred list (if available) or all countries list
                 */
                this.choose(fallbackCountry);
              })
              .finally(() => {
                resolve();
              });
          } else {
            /**
             * 4. Use the first country from preferred list (if available) or all countries list
             */
            this.choose(fallbackCountry);
            resolve();
          }
        });
      },
      /**
       * Get the list of countries from the list of iso2 code
       */
      getCountries(list = []) {
        return list
          .map(countryCode => this.findCountry(countryCode))
          .filter(Boolean);
      },
      findCountry(iso = '') {
        return this.allCountries.find(country => country.iso2.toUpperCase() === iso.toUpperCase());
      },
      getItemClass(index, iso2) {
        const highlighted = this.selectedIndex === index;
        const lastPreferred = index === this.preferredCountries.length - 1;
        const preferred = this.preferredCountries.some(c => c.toUpperCase() === iso2);
        return {
          highlighted,
          'last-preferred': lastPreferred,
          preferred,
        };
      },
      choose(country, toEmitInputEvent = false) {
        let parsedCountry = country;
        if (typeof parsedCountry === 'string') {
          parsedCountry = this.findCountry(parsedCountry);
        }
        if (!parsedCountry) {
          return;
        }
        this.activeCountry = parsedCountry || this.activeCountry || {};
        if (this.phone
          && this.phone[0] === '+'
          && this.activeCountry.iso2
          && this.phoneObject.number.national) {
          // Attach the current phone number with the newly selected country
          this.phone = PhoneNumber(this.phoneObject.number.national, this.activeCountry.iso2)
            .getNumber('international');
        } else if (this.inputOptions && this.inputOptions.showDialCode && parsedCountry) {
          // Reset phone if the showDialCode is set
          this.phone = `+${parsedCountry.dialCode}`;
        }
        if (toEmitInputEvent) {
          this.setNewValue();
        }
      },
      testCharacters() {
        const re = /^[()\-+0-9\s]*$/;
        return re.test(this.phone);
      },
      testCustomValidate() {
        return this.customValidate instanceof RegExp ? this.customValidate.test(this.phone) : false;
      },
      onInput(e) {
        if (this.validCharactersOnly && !this.testCharacters()) {
          return;
        }
        if (this.customValidate && !this.testCustomValidate()) {
          return;
        }
        //this.$refs.input.setCustomValidity(this.phoneObject.valid ? '' : this.invalidMsg);
        // Returns response.number to assign it to v-model (if being used)
        // Returns full response for cases @input is used
        // and parent wants to return the whole response.
        this.setNewValue();
        // Keep the current cursor position just in case the input reformatted
        // and it gets moved to the last character.
        if (e && e.target) {
          this.cursorPosition = e.target.selectionStart;
        }
      },
      // onBlur() {
      //   this.$emit('blur');
      //   this.$emit('onBlur'); // Deprecated
      // },
      // onEnter() {
      //   this.$emit('enter');
      //   this.$emit('onEnter'); // Deprecated
      // },
      // onSpace() {
      //   this.$emit('space');
      //   this.$emit('onSpace'); // Deprecated
      // },
      focus() {
        this.$refs.input.focus();
      },
      toggleDropdown() {
        if (this.disabled) {
          return;
        }
        this.open = !this.open;
      },
      clickedOutside() {
        this.open = false;
      },
      keyboardNav(e) {
        if (e.keyCode === 40) {
          // down arrow
          e.preventDefault();
          this.open = true;
          if (this.selectedIndex === null) {
            this.selectedIndex = 0;
          } else {
            this.selectedIndex = Math.min(this.sortedCountries.length - 1, this.selectedIndex + 1);
          }
          const selEle = this.$refs.list.children[this.selectedIndex];
          if (selEle.offsetTop + selEle.clientHeight
            > this.$refs.list.scrollTop + this.$refs.list.clientHeight) {
            this.$refs.list.scrollTop = selEle.offsetTop
              - this.$refs.list.clientHeight
              + selEle.clientHeight;
          }
        } else if (e.keyCode === 38) {
          // up arrow
          e.preventDefault();
          this.open = true;
          if (this.selectedIndex === null) {
            this.selectedIndex = this.sortedCountries.length - 1;
          } else {
            this.selectedIndex = Math.max(0, this.selectedIndex - 1);
          }
          const selEle = this.$refs.list.children[this.selectedIndex];
          if (selEle.offsetTop < this.$refs.list.scrollTop) {
            this.$refs.list.scrollTop = selEle.offsetTop;
          }
        } else if (e.keyCode === 13) {
          // enter key
          if (this.selectedIndex !== null) {
            this.choose(this.sortedCountries[this.selectedIndex]);
          }
          this.open = !this.open;
        } else {
          // typing a country's name
          this.typeToFindInput += e.key;
          clearTimeout(this.typeToFindTimer);
          this.typeToFindTimer = setTimeout(() => {
            this.typeToFindInput = '';
          }, 700);
          // don't include preferred countries so we jump to the right place in the alphabet
          const typedCountryI = this.sortedCountries
            .slice(this.preferredCountries.length)
            .findIndex(c => c.name.toLowerCase().startsWith(this.typeToFindInput));
          if (typedCountryI >= 0) {
            this.selectedIndex = this.preferredCountries.length + typedCountryI;
            const selEle = this.$refs.list.children[this.selectedIndex];
            const needToScrollTop = selEle.offsetTop < this.$refs.list.scrollTop;
            const needToScrollBottom = selEle.offsetTop + selEle.clientHeight
              > this.$refs.list.scrollTop + this.$refs.list.clientHeight;
            if (needToScrollTop || needToScrollBottom) {
              this.$refs.list.scrollTop = selEle.offsetTop - this.$refs.list.clientHeight / 2;
            }
          }
        }
      },
      reset() {
        this.selectedIndex = this.sortedCountries.map(c => c.iso2).indexOf(this.activeCountry.iso2);
        this.open = false;
      },
      setNewValue() {
        this.$nextTick(()=> {
          this.$emit('input', this.phoneText, this.phoneObject);
          this.$emit('update-number',this.phoneObject);
        })
      }
    },
  };
</script>

<style lang="scss" scoped>
  @import "../../../scss/base";
  .vue-tel-input {
    display: flex;
    text-align: left;
    position: relative;
  }
  .vue-tel-input.disabled .selection,
  .vue-tel-input.disabled .dropdown,
  .vue-tel-input.disabled input {
    cursor: no-drop;
  }
  .vti__dropdown {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    padding: 7px;
    cursor: pointer;
  }
  .vti__dropdown.show {
    max-height: 300px;
    overflow: scroll;
  }
  /*.vti__dropdown.open {*/
  /*  background-color: #f3f3f3;*/
  /*}*/
  /*.vti__dropdown:hover {*/
  /*  background-color: #f3f3f3;*/
  /*}*/
  .vti__selection {
    font-size: .85em;
    display: flex;
    align-items: center;
  }
  .vti__selection .vti__country-code {
    color: #666;
  }
  .c-flag {
    margin-right: 5px;
    margin-left: 5px;
  }
  .vti__dropdown-list {
    z-index: 1;
    padding: 0;
    margin: 0;
    text-align: left;
    list-style: none;
    max-height: 200px;
    overflow-y: scroll;
    position: absolute;
    top: 100%;
    left: -1px;
    right: 0;
    background-color: #fff;
    border: $default-border;
    border-radius: $bdrs;
    font-size: .8em;
  }
  .vti__dropdown-arrow {
    display: inline-block;
  }
  .vti__dropdown-item {
    cursor: pointer;
    padding: 4px 7px;
  }
  .vti__dropdown-item.highlighted {
    background-color: #f3f3f3;
  }
  .vti__dropdown-item.last-preferred {
    border-bottom: 1px solid #cacaca;
  }
  .vti__dropdown-item .c-flag {
    display: inline-block;
    margin-right: 5px;
  }
  .vti__input {
    border: none;
    border-radius: 0 2px 2px 0;
    width: 100%;
    outline: none;
    padding-left: 7px;
  }
  .country-code {
    display: flex;
    align-items: center;
    font-weight: 800;
    margin-bottom: 1px;
  }
  .country-name_native {
    display: inline-flex;
    margin-left: .3em;
  }
</style>
