import {storeActions, i_user, i_userProfile} from '@/store/store'
import {RequestError} from 'visbook-api'
import {requestVB, toRouterUrl} from "@/core/helpers";
import {vue} from "@/main";
import Vue from 'vue'

const namespaced = true;

const state = {
  userAvatar: null,
  userProfile: null
};

const getters = {
  fullName(state:i_user) {
    if(state.userProfile) {
      const fName = state.userProfile.firstName || '';
      const lName = state.userProfile.lastName || '';
      const fn = `${fName} ${lName}`;
      return fn.length>1 ? fn : state.userProfile.email.split('@')[0];
    }
    else return '';
  },
  initials(state:i_user) {
    if(state.userProfile) {
      const fName = getInitials(state.userProfile.firstName);
      const lName = getInitials(state.userProfile.lastName);
      return `${fName} ${lName}`;
    }
    else return '';

    function getInitials(name:string|null):string {
      return name
        ? name.substring(0, 1).toUpperCase()
        : ''
    }
  },
  isAuthorized(state:i_user) {
    return !!state.userProfile;
  }
};

const mutations = {
  set_userAvatar(state:i_user, avatar:any) {
    state.userAvatar = avatar;
  },
  set_userProfile(state:i_user, profile:i_userProfile|null) {
    state.userProfile = profile;
  },
};

const actions = {
  SET_userAvatar({commit}:storeActions, avatar:any) {
    commit('set_userAvatar', avatar);
  },
  EVENT_logOut({commit, dispatch}:storeActions) {
    dispatch('CLEAR_userProfile');
    dispatch('mod_orders/CLEAR_selectedOrderCurrency', null, {root: true});
    return requestVB(vue.$visbook.SEND_logout)
      .then((data:any)=> data)
      .catch((err:RequestError)=> {
        throw err;
      });
  },
  CLEAR_userProfile({commit}:storeActions) {
    commit('set_userProfile', null);
  },
  GET_userProfile({commit}:storeActions) {
    console.log('GET_userProfile send');
    return requestVB(Vue.prototype.$visbook.GET_user)
      .then((response:any[])=> {
        console.log('GET_userProfile OK', response);
        if(response.length) {
          commit('set_userProfile', response[0]);
          return
        }
        else throw {message: vue.$t('page-login_message_unreg')};
      })
      .catch((err:RequestError)=> {
        console.log('GET_userProfile ERROR', err);
        throw err;
      });
  },
  UPDATE_userProfile({commit}:storeActions, profile:any) {
    commit('set_userProfile', profile);
    return requestVB(vue.$visbook.SET_user, profile)
      .then((data:any)=> {
        return })
      .catch((err:RequestError)=> {
        throw err;
      });
  }
};

export default {
  namespaced,
  state,
  getters,
  mutations,
  actions
}
