
























    import {Component, Model, Prop, Vue, Watch} from 'vue-property-decorator';

@Component
export default class BaseInput extends Vue {

  @Prop({default: ''}) value!: string|null;
  @Prop({default: ''}) labelText?: string;
  // @Prop(String) inputPlaceholder?: string;
  @Prop(String) inputId?: string;
  @Prop({type: String, default: ''}) textError?: string;

  inputValidity = false;

  get inputIsValid() {
    return !!this.value && !!this.value.length && this.inputValidity
  }
  get inputPattern() {
    let customPattern = '.*';
    switch (this.$attrs.type) {
      case 'email':
        customPattern = '^([a-z0-9_-]+\\.)*[a-z0-9_-]+@[a-z0-9_-]+(\\.[a-z0-9_-]+)*\\.[a-z]{2,6}$';
        break;
      case 'tel':
        customPattern = '^(\\s*)?(\\+)?([- _():=+]?\\d[- _():=+]?){9,14}(\\s*)?$';
        break;
    }
    return this.$attrs.pattern || customPattern;
  }
  get componentStyles() {
    return {
      'custom-input--number': this.isTypeNumber,
      'custom-input_active': this.value && this.value.length,
      'text-disable': this.$attrs.disabled
    }
  }
  get isTypeNumber() {
    return this.$attrs.type === 'number'
  }
  updateData(data:string | number) {
    this.$emit('input', data);
    const input = this.$refs.inputDom as HTMLInputElement;
    if(input) this.inputValidity = input.validity.valid;
    this.$nextTick(()=> {
      this.$emit('input-validity', this.inputIsValid);
    })
  }
  submitInput() {
    if(this.inputIsValid) this.$emit('send-form')
  }
  focus() {
    const input = this.$refs.inputDom as HTMLInputElement;
    input.focus();
  }
  valueInc() {
    const max = this.$attrs.max || Infinity;
    let val = 1;
    if(this.value) {
      val = (+this.value < +max) ? +this.value + 1 : +max;
    }
    this.updateData(val.toString())
  }
  valueDec() {
    const min = this.$attrs.min || 0;
    let val = 0;
    if(this.value) {
      val = (+this.value > +min) ? +this.value - 1 : +min;
    }
    this.updateData(val.toString())
  }
}
