
















import {Component, Prop, Vue, Model, Watch} from 'vue-property-decorator';
import BasePicker from 'vue-hotel-datepicker';
import {notification_type} from "@/store/store";
import {twoDigits} from "@/core/helpers";
import store from "@/store";
interface i_modelData {
  checkIn: Date|null
  checkOut: Date|null
}

@Component({
  components: {BasePicker}
})

export default class HotelDatePicker extends Vue {
  @Model('input', { type: Object }) value!: i_modelData;
  $store:any=store;
  modelData:i_modelData = {
    checkIn: null,
    checkOut: null
  };

  get settings() {
    if(this.$store.state.mod_company.companySettings) {
      return this.$store.state.mod_company.companySettings.timeSettings;
    }
  }
  get startBooking() {
    if(this.settings.openingDate) {
      const settingsDate = new Date(this.settings.openingDate);
      const currentDate = new Date();
      return currentDate > settingsDate ? currentDate : settingsDate
    }
    else return new Date()
  }
  get stopBooking() {
    return this.settings.openingDate ? this.settings.openingDate : Infinity;
  }
  get minNights() {
    return this.settings.minimumBookingLength || 1;
  }

  @Watch('input', { immediate: true, deep: true })
  saveInside(newData:i_modelData) {
    this.modelData = this.value;
  }

  updateInfo(data:Date, isInput:boolean) {
    isInput
      ? this.modelData.checkIn = data
      : this.modelData.checkOut = data;
    this.$emit('input', this.modelData);
  }
}
