















import {Component, Vue} from 'vue-property-decorator';
import TemplateBase     from '@/pages/templates/template-base.vue';
import TheQuickBooking  from "@/components/the-quick-booking.vue";
import BaseInput        from "@/components/forms/base-input.vue";
import store from "@/store";
@Component({
  components: {TheQuickBooking, TemplateBase, BaseInput}
})
export default class PageHome extends Vue {
  $store:any=store;
  mounted() {
    const introEl = this.$refs.introEl as HTMLDivElement;
    this.introSize.width = introEl.offsetWidth;
    this.introSize.height = introEl.offsetHeight;
  }

  introSize = {
    width: 0,
    height: 0
  };

  get setupInfo() {
    return this.$store.state.mod_company.companySettings;
  }
  get introStyles() {
    let srcBg = '';
    if(this.setupInfo && this.setupInfo.images.length) {
      const companyBg = this.setupInfo.images.filter((el:any)=> el.imageType === "background"); //TODO add i out_setup-image
      if(companyBg.length) {
        const transformer = companyBg[0].transformer;
        const positionUpload = transformer.indexOf('/visdoc');
        const cropperData = `/f_png/w_${this.introSize.width},h_${this.introSize.height},c_lfill,f_auto,g_auto:subject`;
        const url = positionUpload >= 0
          ? `${transformer.slice(0, positionUpload)}${cropperData}${transformer.slice(positionUpload)}`
          : transformer;
        srcBg = `${url}/${companyBg[0].imagePath}`;
      }
    }
    const bg = srcBg || require('@/assets/img/intro-bg.png');
    return {
      background: `linear-gradient(rgba(22,22,22,0.3), rgba(22,22,22,0.3)), url(${bg}) center/cover no-repeat`
    }
  }
  search() {
    const quickBooking = this.$refs.quickBooking as any;
    quickBooking.search()
  }
}
