












































































import {Component, Vue, Watch} from 'vue-property-decorator';
import {RequestError, in_loginPhone}         from 'visbook-api';
import {deepCopy, requestVB, toRouterUrl}  from '@/core/helpers.ts';

import TheLanguage      from '@/components/the-language.vue';
import BaseInput        from '@/components/forms/base-input.vue';
import BaseRadio        from '@/components/forms/base-radio.vue';
import BaseInputPhone   from '@/components/forms/base-input-phone/base-input-phone.vue';
import store from "@/store";

enum LoginSteps{
  GetContact,
  EnterCode
}
interface inputFields {
  value: string;
  error?: string;
  done?: boolean;
  valid: boolean;
  meta?: {}
}
interface i_phoneInfo {
  number: i_phoneInfo_number,
  country: i_phoneInfo_country,
}
interface i_phoneInfo_number {
  significant: string
}
interface i_phoneInfo_country {
  dialCode: string
}

@Component({
  components: {TheLanguage, BaseInput, BaseRadio, BaseInputPhone }
})
export default class Login extends Vue {
  $store:any=store;
  mounted() {
    const sidebarEl = this.$refs.sidebarEl as HTMLDivElement;
    this.sidebarSize.width = sidebarEl.offsetWidth;
    this.sidebarSize.height = sidebarEl.offsetHeight;
  }

  checkedIsEmail = true;
  defaultInput:inputFields  = {value: '', error: '', done: false, valid: false};
  inputEmail:inputFields    = {value: '', error: '', done: false, valid: false};
  inputPhone:inputFields    = {value: '', error: '', done: false, valid: false};
  inputPass:inputFields     = {value: '', error: '', done: false, valid: false};

  sidebarSize = {
    width: 0,
    height: 0
  };

  phoneInfo = {
    phone: '',
    code: ''
  };
  modal = true;

  steps = LoginSteps;
  step:LoginSteps = LoginSteps.GetContact;

  get loginBtnDisabled() {
    if(this.step === LoginSteps.GetContact && (this.inputEmail.valid || this.inputPhone.valid)
      || this.step === LoginSteps.EnterCode && this.inputPass.valid
    ) return false;
    else return true;
  }
  get userIsLogin() {
    return this.$store.getters['mod_user/isAuthorized'];
  }
  get setupInfo() {
    return this.$store.state.mod_company.companySettings;
  }
  get sidebarStyles() {
    let srcBg = '';
    if(this.setupInfo && this.setupInfo.images.length) {
      const companyBg = this.setupInfo.images.filter((el:any)=> el.imageType === "background"); //TODO add i out_setup-image
      const transformer = companyBg[0].transformer;
      const positionUpload = transformer.indexOf('/visdoc');
      const cropperData = `/f_png/w_${this.sidebarSize.width},h_${this.sidebarSize.height},c_lfill,f_auto,g_auto:subject`;
      const url = positionUpload >= 0
        ? `${transformer.slice(0, positionUpload)}${cropperData}${transformer.slice(positionUpload)}`
        : transformer;
      srcBg = `${url}/${companyBg[0].imagePath}`;
    }
    const bg = srcBg || require('@/assets/login-page-bg.png');
    return {
      background: `linear-gradient(rgba(51,51,51,0.68), rgba(51,51,51,0.68)), url(${bg}) center/cover no-repeat`
    }
  }

  @Watch('checkedIsEmail')
  onCheckedIsEmail() {
    this.inputEmail = this.setDefaultInput();
    this.inputPhone = this.setDefaultInput();
  }
  @Watch('userIsLogin', {immediate: true})
  checkUserIsLogin(newVal:boolean) {
    if(newVal) toRouterUrl('orders')
  }

  requestSwitch() {
    if(this.step === LoginSteps.GetContact) {
      this.checkedIsEmail ? this.requestEmail() : this.requestSMS()
    }
    else if(this.step === LoginSteps.EnterCode && this.inputPass.valid) this.requestCode();
  }
  resendCode() {
    this.step = LoginSteps.GetContact;
    this.inputPass = this.setDefaultInput();
    this.requestSwitch()
  }
  getPhoneNum(e:i_phoneInfo) {
    if(e.number.significant) this.phoneInfo.phone = e.number.significant;
    if(e.country.dialCode) this.phoneInfo.code = e.country.dialCode;
  }
  requestEmail() {
    console.log('requestEmail send', this.inputEmail.value);
    requestVB(this.$visbook.SEND_email, this.inputEmail.value)
      .then(()=> {
        console.log('requestEmail OK');
        this.firstRequestSuccess()
      })
      .catch((error:RequestError)=> {
        console.log('requestEmail ERROR', error);
        if(error.data.Email) this.inputEmail.error = error.data.Email[0];
        else this.inputEmail.error = error.message;
      })
  }
  requestSMS() {
    const reqData:in_loginPhone = {
      code: +this.phoneInfo.code,
      phone: +this.phoneInfo.phone
    };
    console.log('requestSMS send', reqData);
    requestVB(this.$visbook.SEND_sms, reqData)
      .then(()=> {
        console.log('requestSMS OK');
        this.firstRequestSuccess()
      })
      .catch((error:RequestError)=> {
        console.log('requestSMS ERROR', error);
        if(error.data.error) this.inputPhone.error = error.data.error;
        else this.inputPhone.error = error.message;
      });
  }
  requestCode() {
    const email = this.$visbook.GET_validationEmail;
    const sms = this.$visbook.GET_validationSMS;
    console.log('requestCode send', this.inputPass.value);
    requestVB(this.checkedIsEmail ? email : sms, this.inputPass.value)
      .then(()=> {
        console.log('requestCode OK');
        this.$store.dispatch('mod_globalView/CLOSE_modal');
        return this.$store.dispatch('mod_user/GET_userProfile')
      })
      .catch((error:RequestError)=> {
        console.log('requestCode ERROR', error);
        this.inputPass.error = (error.status === 401)
          ? this.$t('page-login_form-code-error') as string
          : error.message;
      });
  }
  firstRequestSuccess() {
    this.step = this.steps.EnterCode;
    this.inputEmail.done = true;
    this.inputPhone.done = true;
    this.inputPass = this.setDefaultInput();
    this.setFocusInputCode();
  }
  setDefaultInput():inputFields {
    return deepCopy(this.defaultInput)
  }
  setValidity(variable:inputFields, value:boolean) {
    variable.valid = value;
  }
  setFocusInputCode() {
    const input = this.$refs.inputCodeEl as HTMLInputElement;
    this.$nextTick(()=> {
      input.focus();
    })

  }
}
