











import {Component, Vue, Prop} from 'vue-property-decorator';

@Component
export default class DatePeriod extends Vue {

  @Prop({required: true}) dataFrom!:string;
  @Prop({required: true}) dataTo!:string;

  get sameDay() {  //check if activity will be during a single day
    if(!this.hasData) return false;
    let date = Vue.filter('date');
    return date(this.dataFrom) === date(this.dataTo);
  }
  get sameTime() {  //check if activity will be during a single day
    if(!this.hasData) return false;
    let time = Vue.filter('time');
    return time(this.dataFrom) === time(this.dataTo);
  }
  get hasData() {
    return this.dataFrom && this.dataTo;
  }

}
