









import {Component, Vue} from 'vue-property-decorator';
import store from "@/store";
@Component({})
export default class TheLogo extends Vue {
  $store:any=store;
  get companyInfo() {
    return this.$store.state.mod_company.companyInfo;
  }
  get setupInfo() {
    return this.$store.state.mod_company.companySettings;
  }
  get logo() {
    let company = this.companyInfo;
    if(this.setupInfo
      && this.setupInfo.images
      && this.setupInfo.images.length
    ) {
      let url = this.setupInfo.images[0].transformer;
      let path = this.setupInfo.images[0].imagePath;
      return `<img class="header-main_logo-img" alt="logo" src="${url}/${path}">`
    }
    else if(company) {
       return company.name;
    }
    else null;
  }
}
